import React from 'react';

function Images({ images, setIndex }) {
  return (
    <div className="changeImg">
      {images.map((img, i) => {
        return (
          <img
            src={img}
            alt=""
            key={i}
            onClick={() => {
              setIndex(i);
            }}
          />
        );
      })}
    </div>
  );
}

export default Images;
