import React, { useContext, useState } from 'react';
import { DataContext } from '../DataProvider';
import { useHistory, useParams } from 'react-router';
import '../css/Payment.css';
import { Link } from 'react-router-dom';
import Footer from '../footer/Footer';

function Payment() {
  let history = useHistory();
  const { id } = useParams();
  const value = useContext(DataContext);
  const [products, setProducts] = value.products;

  const getItem = products.find((item) => {
    return item.id == id;
  });

  // const dataCart = JSON.parse(localStorage.getItem('cart'));

  // const buyItem = [getItem, ...dataCart]; // 구매하기 + 장바구니제품

  // console.log(buyItem.includes(undefined));
  // console.log(buyItem.splice(1));

  return (
    <>
      <div className="payment">
        <div className="memberInfo">
          <h2>Checkout</h2>
          <div className="customer">
            <h3>Customer</h3>
            <Link to="/login">
              <button>Log in</button>
            </Link>
          </div>
          <div className="delivery">
            <form className="row">
              <h3>Delivery location</h3>
              <div className="box">
                <div className="box1">
                  <label className="label">The recipient</label>
                  <input
                    type="email"
                    className="form-control1"
                    placeholder="name"
                  />
                </div>
                <div className="box2">
                  <label className="label">Phone number</label>
                  <input
                    type="password"
                    className="form-control1"
                    placeholder="- without"
                  />
                </div>
              </div>
              <div>
                <label className="label">Address</label>
                <div className="address">
                  <input type="text" className="form-control2" />
                  <button>Search</button>
                </div>
                <input
                  type="text"
                  className="form-control addressInput"
                  id="inputAddress"
                  placeholder="rest of the address"
                />
              </div>
              <div>
                <label className="label">Delivery memo</label>
                <input type="text" className="form-control" />
              </div>
              <div className="goCartBtn">
                <button
                  onClick={() => {
                    history.push('/cart');
                  }}
                >
                  Go to cart
                </button>
              </div>
            </form>
          </div>

          <div className="productInfo">
            <h3>Order details</h3>
            <div className="box">
              <div className="box1">
                <img src={getItem.src[0]} />
              </div>
              <div className="box2">
                <h2>{getItem.title}</h2>
                <p>Style ID : {getItem.style}</p>
                <span className="count">Quantity : {getItem.count}</span>
                <span className="price">
                  &#8361;{getItem.price.toLocaleString()}
                </span>
              </div>
            </div>
            <div className="paymentInfo">
              <div className="paymentAmount">Total :</div>
              <span className="price">
                &#8361;{getItem.price.toLocaleString()}
              </span>
            </div>
          </div>

          <div className="nextStep">
            <button className="nextStepBtn">Next step</button>
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
}

export default Payment;
