import React from 'react';
import { Link } from 'react-router-dom';
import { Button } from './Button';
import '../css/Footer.css';

function Footer() {
  return (
    <div className="footer-container">
      <section className="footer-subscription">
        <div className="input-areas">
          <form>
            <input
              className="footer-input"
              name="email"
              type="email"
              placeholder="Your E-mail"
            />
            <Button buttonStyle="btn--outline">Subscribe</Button>
          </form>
        </div>
      </section>
      <div class="footer-links">
        <div className="footer-link-wrapper">
          <div class="footer-link-items">
            <h2>service</h2>
            <Link to="/">Shipping and delivery</Link>
            <Link to="/">Payment options</Link>
            <Link to="/">Returns and exchanges</Link>
          </div>
          <div class="footer-link-items">
            <h2>connect</h2>
            <Link to="/">Ask us a question here</Link>
            <Link to="/">+44(0113) 327 4627</Link>
            <Link to="/">austin@google.com</Link>
          </div>
        </div>
        <div className="footer-link-wrapper">
          <div class="footer-link-items">
            <h2>about us</h2>
            <Link to="/">About us</Link>
            <Link to="/">Stores</Link>
            <Link to="/">Careers</Link>
            <Link to="/">Editorials</Link>
          </div>
        </div>
      </div>
      <section class="social-media">
        <div class="social-media-wrap">
          <div class="footer-logo">
            <Link to="/" className="social-logo">
              Austin
            </Link>
          </div>
          <small class="website-rights">Austin © 2021</small>
          <div class="social-icons">
            <Link
              class="social-icon-link facebook"
              to="/"
              target="_blank"
              aria-label="Facebook"
            >
              <i class="fab fa-facebook-f" />
            </Link>
            <Link
              class="social-icon-link instagram"
              to="/"
              target="_blank"
              aria-label="Instagram"
            >
              <i class="fab fa-instagram" />
            </Link>
            <Link
              class="social-icon-link youtube"
              to="/"
              target="_blank"
              aria-label="Youtube"
            >
              <i class="fab fa-youtube" />
            </Link>
            <Link
              class="social-icon-link twitter"
              to="/"
              target="_blank"
              aria-label="Twitter"
            >
              <i class="fab fa-twitter" />
            </Link>
            <Link
              class="social-icon-link twitter"
              to="/"
              target="_blank"
              aria-label="LinkedIn"
            ></Link>
          </div>
        </div>
      </section>
    </div>
  );
}

export default Footer;
