import React, { useContext, useState } from 'react';
import { DataContext } from './DataProvider';
import { Link } from 'react-router-dom';
import './css/Header.css';

function Header() {
  const [menu, setMenu] = useState(false);
  const value = useContext(DataContext);
  const [cart, setCart] = value.cart;

  const clickMenu = () => {
    setMenu(!menu);
  };

  const clickMenuBtn = () => {
    setMenu(false);
  };

  return (
    <>
      <header>
        <div className="header-menu" onClick={clickMenu}>
          <i className="fas fa-bars"></i>
        </div>
        <div className="header-logo">
          <h1>
            <Link to="/">Austin</Link>
          </h1>
        </div>
        <nav>
          <ul className={menu ? 'toggle' : ''}>
            <li>
              <Link to="/" onClick={clickMenuBtn}>
                Home
              </Link>
            </li>
            <li>
              <Link to="/product" onClick={clickMenuBtn}>
                Product
              </Link>
            </li>
            <li>
              <Link to="/about" onClick={clickMenuBtn}>
                About
              </Link>
            </li>
            <li>
              <Link to="/login" onClick={clickMenuBtn}>
                Login
              </Link>
            </li>
            <li className="header-close" onClick={clickMenu}>
              <i className="fas fa-times"></i>
            </li>
          </ul>
          <div className="header-cart">
            <span>{cart.length}</span>
            <Link to="/cart">
              <i className="cartIcon fas fa-shopping-cart"></i>
            </Link>
          </div>
        </nav>
      </header>
    </>
  );
}

export default Header;
