import React from 'react';
import './css/RegisterForm.css';

function RegisterForm() {
  return (
    <div className="registerForm">
      <div className="registerInfo">
        <h2>Create account</h2>
        <div className="registerInput">
          <h3>First name*</h3>
          <input type="text" required />
          <h3>E-mail*</h3>
          <input type="password" required />
          <h3>Password*</h3>
          <input type="password" required />
        </div>
        <div className="desc">
          <span>
            By creating an account with Austin, you will be able to move through
            the checkout process faster, store multiple shipping addresses, view
            and track your orders in your account, and much more.
          </span>
        </div>
        <button className="registerBtn">Sign up</button>
        <div className="or">or</div>
        <button className="GoogleBtn">
          <img src="https://img1.daumcdn.net/thumb/R1280x0.fjpg/?fname=http://t1.daumcdn.net/brunch/service/user/5rH/image/aFrEyVpANu07FvoBZQbIB4aF_uc" />
          Continue with Google
        </button>
        <button className="FacebookBtn">
          <img src="https://blog.kakaocdn.net/dn/lhU48/btqRoQfJhbZ/9E6G4WxknrC7MPv2gV1DSk/img.jpg" />
          Continue with Facebook
        </button>
        <button className="TwitterBtn">
          <img src="https://blog.kakaocdn.net/dn/AcIGI/btqxtp2xkg2/EGABG3i2NAMq3kRu1VaGzk/img.jpg" />
          Continue with Twitter
        </button>
      </div>
    </div>
  );
}

export default RegisterForm;
