import React from 'react';
import '../css/Slider.css';

function SliderBtn({ nextSlide, prevSlide }) {
  return (
    <div>
      <span onClick={nextSlide} className="btn-slide next">
        <i class="fas fa-chevron-right"></i>
      </span>
      <span onClick={prevSlide} className="btn-slide prev">
        <i class="fas fa-chevron-left"></i>
      </span>
    </div>
  );
}

export default SliderBtn;
