import React, { useContext, useState } from 'react';
import { DataContext } from '../DataProvider';
import { useParams } from 'react-router';
import { Link } from 'react-router-dom';
import '../css/Details.css';
import Images from './Images';
import Footer from '../footer/Footer';
import sizeGuide from '../item/sizeGiude.JPG';

function Details() {
  const { id } = useParams(); // /:id 자리에 사용자가 입력한 값
  const value = useContext(DataContext);
  const [products, setProducts] = value.products;
  const [index, setIndex] = useState(0);
  const [tab, setTab] = useState(0);
  const addCart = value.addCart;

  // 클릭한 상품 id
  const getItem = products.find((item) => {
    return item.id == id;
  });

  return (
    <>
      <div className="details">
        <div className="imgBox">
          <img src={getItem.src[index]} alt="" />
          <Images images={getItem.src} setIndex={setIndex} />
        </div>
        <div className="box">
          <div className="row">
            <h2>{getItem.title}</h2>
            <span>&#8361;{getItem.price.toLocaleString()}</span>
            <p>{getItem.description}</p>
            <p>Style ID : {getItem.style}</p>
            <div>
              {getItem.size.map((size, i) => {
                return (
                  <button key={i} className="size">
                    {size}
                  </button>
                );
              })}
            </div>
            <Link
              to="/cart"
              className="addCart"
              onClick={() => {
                addCart(getItem.id);
              }}
            >
              Add to cart
            </Link>
            <Link to={`/payment/${getItem.id}`} className="buyBtn">
              Buy now
            </Link>
          </div>
        </div>
      </div>
      <div className="shipping">
        <ul class="nav-ul">
          <li class="nav-li">
            <a
              className={tab === 0 ? 'nav-a active' : 'nav-a'}
              onClick={() => {
                setTab(0);
              }}
            >
              Delivery
            </a>
          </li>
          <li class="nav-li">
            <a
              className={tab === 1 ? 'nav-a active' : 'nav-a'}
              onClick={() => {
                setTab(1);
              }}
            >
              Size guide
            </a>
          </li>
          <li class="nav-li">
            <a
              className={tab === 2 ? 'nav-a active' : 'nav-a'}
              onClick={() => {
                setTab(2);
              }}
            >
              Support
            </a>
          </li>
        </ul>
        <TabContent tab={tab} />
      </div>
      <Footer />
    </>
  );
}

function TabContent(props) {
  if (props.tab === 0) {
    return (
      <div>
        <ul>
          <h3>We've got your back</h3>
          <li>One delivery fee</li>
          <li>
            Free returns within 14 days (excludes customized pieces and face
            masks)
          </li>
          <li>All our deliveries are climate conscious</li>
        </ul>
        <h3>Import duties information</h3>
        <p>Let us handle the legwork.</p>
        <p>
          Delivery duties are included in the item price when shipping to all EU
          countries plus The United Kingdom, USA, Canada, China Mainland,
          Australia, New Zealand, Puerto Rico, Switzerland, Singapore, Republic
          Of Korea, Kuwait, Mexico, Qatar, India, Norway, Saudi Arabia, Taiwan
          Region, Thailand, U.A.E., Japan, Brazil, Isle of Man, San Marino,
          Colombia, Chile, Argentina, Egypt, Lebanon, Hong Kong SAR, the Canary
          Islands and Bahrain. All import duties are included in your order –
          the price you see is the price you pay.
        </p>
      </div>
    );
  } else if (props.tab === 1) {
    return (
      <div>
        <img className="sizeGuide" src={sizeGuide} />
      </div>
    );
  } else if (props.tab === 2) {
    return (
      <div>
        <p className="support">
          Do you have any questions regarding this product, or have you found
          something wrong in the description of it? Contact us, and don't forget
          to include the article number.
        </p>
      </div>
    );
  }
}

export default Details;
