import React, { useContext, useEffect, useState } from 'react';
import { DataContext } from '../DataProvider';
import { Link } from 'react-router-dom';
import '../css/Cart.css';
import Size from './Size';
import Payment from './Payment';
import Footer from '../footer/Footer';

function Cart() {
  const value = useContext(DataContext);
  const [cart, setCart] = value.cart;
  const [total, setTotal] = useState(0);

  // console.log(cart);

  useEffect(() => {
    const getTotal = () => {
      const res = cart.reduce((prev, item) => {
        const multi = item.price * item.count;
        return prev + multi;
      }, 0);
      setTotal(res.toLocaleString()); // 원화 표시
    };
    getTotal();
  }, [cart]);

  // 수량 감소
  const reduce = (id) => {
    cart.forEach((item) => {
      if (item.id === id) {
        item.count === 1 ? (item.count = 1) : (item.count -= 1);
      }
    });
    setCart([...cart]);
  };

  // 수량 증가
  const increase = (id) => {
    cart.forEach((item) => {
      if (item.id === id) {
        item.count += 1;
      }
    });
    setCart([...cart]);
  };

  // 상품 삭제
  const remove = (id) => {
    if (window.confirm('정말 삭제하시겠습니까?')) {
      cart.forEach((item, i) => {
        if (item.id === id) {
          cart.splice(i, 1);
        }
      });
      setCart([...cart]);
    }
  };

  return (
    <>
      <div className="cartBox">
        {cart.map((item, i) => {
          return (
            <div className="cart" key={item.id}>
              <div className="imgBox">
                <img src={item.src[0]} alt="" />
              </div>
              <div className="box">
                <div className="row">
                  <h2>{item.title}</h2>
                  <span className="price">
                    &#8361;{item.price.toLocaleString()}
                  </span>
                  {/* <p>{item.description}</p> */}
                  <p>Style ID : {item.style}</p>
                  <Size size={item.size} />

                  <div className="count">
                    <button
                      className="countBtn"
                      onClick={() => {
                        reduce(item.id);
                      }}
                    >
                      -
                    </button>
                    <span>{item.count}</span>
                    <button
                      className="countBtn"
                      onClick={() => {
                        increase(item.id);
                      }}
                    >
                      +
                    </button>
                  </div>

                  <div
                    className="delete"
                    onClick={() => {
                      remove(item.id);
                    }}
                  >
                    ✖
                  </div>
                </div>
              </div>
            </div>
          );
        })}
      </div>
      <div className="total">
        <h3>Total : &#8361;{total}</h3>
        <Link to="/payment">Payment</Link>
      </div>
      <Footer />
    </>
  );
}

export default Cart;
