import React, { useState } from 'react';
import sliderData from './sliderData';
import '../css/Slider.css';
import SliderBtn from './SliderBtn';

function Slider() {
  const [slideIndex, setSlideIndex] = useState(1);

  const nextSlide = () => {
    if (slideIndex !== sliderData.length) {
      setSlideIndex(slideIndex + 1);
    } else if (slideIndex === sliderData.length) {
      setSlideIndex(1);
    }
  };

  const prevSlide = () => {
    if (slideIndex !== 1) {
      setSlideIndex(slideIndex - 1);
    } else if (slideIndex === 1) {
      setSlideIndex(sliderData.length);
    }
  };

  const moveDot = (i) => {
    setSlideIndex(i);
  };

  return (
    <div className="sliderImg">
      {sliderData.map((img, i) => {
        return (
          <div
            className={slideIndex === i + 1 ? 'slide active-anim' : 'slide'}
            key={img.id}
          >
            <img src={process.env.PUBLIC_URL + `/images/img${i + 1}.jpg`} />
          </div>
        );
      })}
      <SliderBtn nextSlide={nextSlide} prevSlide={prevSlide} />

      <div className="sliderDot">
        {Array.from({ length: 5 }).map((item, i) => {
          return (
            <div
              onClick={() => moveDot(i + 1)}
              className={slideIndex === i + 1 ? 'dot active' : 'dot'}
            ></div>
          );
        })}
      </div>
    </div>
  );
}

export default Slider;
