import React from 'react';
import '../css/Filter.css';

function Filter({ filter }) {
  return (
    <div className="filter">
      <div className="brand">
        <ul>
          <li
            className="list active"
            onClick={() => {
              filter('all');
            }}
          >
            All
          </li>
          <li
            className="list"
            onClick={() => {
              filter('nike');
            }}
          >
            Nike
          </li>
          <li
            className="list"
            onClick={() => {
              filter('adidas');
            }}
          >
            Adidas
          </li>
          <li
            className="list"
            onClick={() => {
              filter('vans');
            }}
          >
            Vans
          </li>
        </ul>
      </div>
    </div>
  );
}

export default Filter;
